.h-1
{
    font-size:2rem;
    font-weight: bold;
    color: rgb(124, 124, 124);
    padding-top: 1rem;

    font-family: 'Poppins', sans-serif;; text-align: center; text-transform: uppercase;
}
.s-1
{
    height: 50px;
    text-align: center;
    background-color: #e6f5ff;
}


.project-layout
{
    display: grid;
    grid-template-columns: 120px 1fr 1fr 100px;
    
    padding: 2rem;
    margin: 20px;
    padding-bottom: 90px;
    background-color: #e6f5ff;
    

}
.p-heading-1
{
    font-size: 3rem;
    word-spacing: 0px;
    font-weight: 800;
    font-family: 'Poppins', sans-serif;


}
.content
{
    display: grid;
    grid-template-rows:100px 1fr;
}
.c-1
{
   display: grid;
   grid-template-columns: 0.7fr 3fr 1fr;
}
.desc
{
    text-align: justify;
    font-size: 1rem;
    line-height: 30px;
}
.middle
{
    padding-top: 4.5rem;
}




.container {
    margin: 0 auto;
    width: 110%;
    text-align: center;
    font-size: 0;
    font-family: Helvetica;
    font-weight: 400;

    text-align: right;
    padding: 2rem;
}

h2.title {
    position: relative;
   

    color: #222222;
}

h2.title::after {
    position: absolute;
    content: "";
    width: 50px;
    height: 3px;
    left: calc(50% - 25px);
    bottom: 0;
    background: #222222;
}

p {
    position: relative;

    font-size: 16px;
    color: #222222;
}

.btn {
    position: relative;
    display: inline-block;

    padding: 12px 27px;
    text-align: center;
    font-size: 16px;
    letter-spacing: 1px;
    text-decoration: none;
    color: #999999;
    background: #ffffff;
    border: 3px solid #999999;
    cursor: pointer;
    transition: ease-out 0.5s;
    -webkit-transition: ease-out 0.5s;
    -moz-transition: ease-out 0.5s;
}


.btn.btn-border-2::after,
.btn.btn-border-2::before {
    position: absolute;
    content: "";
    width: 0;
    height: 0;
    transition: .5s;
}

.btn.btn-border-2::after {
    top: -3px;
    left: -3px;
    border-top: 3px solid transparent;
    border-left: 3px solid transparent;
}

.btn.btn-border-2::before {
    bottom: -3px;
    right: -3px;
    border-bottom: 3px solid transparent;
    border-right: 3px solid transparent;
}

.btn.btn-border-2:hover {
    color: #222222;
}

.btn.btn-border-2:hover::after,
.btn.btn-border-2:hover::before {
    width: calc(50% + 3px);
    height: calc(50% + 3px);
    border-color: #222222;
}


.project-image
{
    height: 90%;
    padding: 2rem;
    margin-left: auto;
    margin-right: auto;

    
}
.image {
    position: relative;
    
    cursor: pointer;

  }

  .project-image{
   
    object-fit: cover;
    transition: transform 0.5s ease-in-out;
  }

  .image:hover .project-image {
    transform: scale(1.1);
  }




  @media screen and (max-width: 1541px) {
    .p-heading-1{
        font-size: 2rem;
    }
 
    .middle
    {   
        padding-top:1rem ;
    }
    .c-1{
        display: grid;
        grid-template-columns: 10px 1fr 10px;
    }
    .project-layout
    {
        display: grid;
        grid-template-columns: 50px 1fr 1fr 50px;
    }
}
  

@media screen and (max-width: 1146px) {
    .project-image{
        height: 80%;
    }
    .project-layout
    {
        padding-left: 0%;
        padding-right: 0%;
    }
}
  


@media screen and (max-width: 947px) {
 .project-layout
 {
    display: flex;
    flex-direction: column;
    position: relative;
    margin-left: 0px;
    margin-right: 0px;
    padding: 1rem;
   
    
 }

 .project-image
 {
    height: 390px;
    width: 390px;
    padding-top: 1rem;
   
 }
 .image
 {
    height: 390px;
 
 }

}
  

@media screen and (max-width: 695px) {
  
   
    .project-image
    {
       height: 290px;
       width: 350px;
       padding-top: 2rem;
      
    }
    .image
    {
       height: 290px;
    
    }
   .container
   {
    width: 100%;
   }
   }
