.introduction
{
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 2fr 100px;
    margin-top: 5rem; 
    overflow: hidden;
    background-color:#e6f5ff;
}
.profile_pic
{
    height: 290px;
    width: 250px;
    padding-top: 2rem;
    
   float: right;
}

.intro
{

    display: grid;
    grid-template-rows: 50px 1fr;
}
.heading
{ padding-left: 5rem;
    float: left;
    font-size: 35px;
    font-weight: bold;
   font-family: 'Poppins', sans-serif;
    color: #3498db;
   
    text-decoration: underline;
    text-decoration-thickness: 0.05rem;
    text-underline-offset: 3px;
    

}
.content-contain
{
    height: 100%;
    padding:5rem ;
    
    padding-top: 2rem;
    text-align: justify;
    font-family: Helvetica;
    text-justify: newspaper;
  


}
.contents
{
    font-size: 20px;
    height: 100%;
    
    font-family: Helvetica;
    color: #3f413f;
    word-spacing: 5px;
    line-height: 31px;
    text-align: left;
    
   
}



.new_introduction
{
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-rows: 290px 2fr 100px;
    margin-top: 3rem; 
    overflow: hidden;
    background-color:#e6f5ff;
}



.new_img
{
    margin-left: auto;
    margin-right: auto;
   

}

.new_profile_pic
{
    height: 250px;
    width: 250px;
}

.new_content-contain
{
    padding: 2rem;
    text-align: justify;
    word-break: break-all;

    font-family:Helvetica;
   


    color: #000000;
    font-family: Helvetica;
    font-size: 16px;
    font-weight: 500;
    -webkit-font-smoothing: antialiased;

    
}



.b-v
{    padding-top:15px ;
    height: 1px;
    width: 100%;
    border-bottom: 1px solid #e5e7eb;;
}

.scholar
{font-size: 1.6rem;}
/*
  Breakpoints
*/



@media screen and (max-width: 1250px) {
    .profile-pic{
        width:300px;
        height:300px;
      
    }
    .content-contain
    {
        padding: 3rem;
        padding-right:2rem ;
        padding-top: 2rem;
    }
    .heading{
        padding-left: 3rem;
    }

.introduction 
{
grid-template-columns: 1fr 2fr 80px;
}
}

@media screen and (max-width: 1000px) {
    .profile-pic{
        width:250px;
        height:250px;
      
    }
    .content-contain
    {
        padding: 3rem;
        padding-right:1rem ;
        padding-top: 3rem;
    }
    
   .heading
   {
    padding-left: 2rem;
   }

.contents
{
    font-size: 20px;
    text-align: justify;
}

.introduction 
{
grid-template-columns: 1fr 2fr 70px;
}
}





@media screen and (max-width: 850px) {
    .profile-pic{
        width:200px;
        height:200px;
      
    }
    .content-contain
    {
        padding: 3rem;
        padding-right:1rem ;
        padding-top: 3rem;
    }
    

.introduction 
{
grid-template-columns: 1fr 2fr 60px;
}
.contents
{
    font-size: 20px;
}


}





