@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

body{
	font-family:Helvetica;
}
.timeline-section{
    background-color: #e6f5ff;
   
	

	padding: 100px 15px;
}
.timeline-items{
	max-width: 1000px;
	margin:auto;
	display: flex;
	flex-wrap: wrap;
	position: relative;
}
.timeline-items::before{
	content: '';
	position: absolute;
	width: 2px;
	height: 100%;
	background-color: #2d3135;
	left: calc(50% - 1px);

}


.text-para

{
	font-family: Helvetica;
}
.timeline-content h3
{font-family: Helvetica;

}
.Education
{
	font-family: 'Poppins', sans-serif;
}
.name li
{
	font-family: Helvetica;
}


.timeline-item{
	margin-bottom: 40px;
	width: 100%;
	position: relative;
}
.timeline-item:last-child{
	margin-bottom: 0;
}
.timeline-item:nth-child(odd){
    padding-right: calc(50% + 30px);
	text-align: right;
}
.timeline-item:nth-child(even){
    padding-left: calc(50% + 30px);
}
.timeline-dot{
	height: 16px;
	width: 16px;
    background-color: rgb(59, 171, 245);
	position: absolute;
	left: calc(50% - 8px);
	border-radius: 50%;
	top:10px;
}
.timeline-date{
	font-size: 18px;
	color: #111111;
	margin:6px 0 15px;
    text-align: center;
}
.timeline-content{
    
   
	padding: 30px;
    text-align: left;
    
	
    background-color: #ffffff;
 
    -webkit-box-shadow: 10px 10px 20px -3px rgba(0,0,0,0.4);
    -moz-box-shadow: 10px 10px 20px -3px rgba(0,0,0,0.4);
    box-shadow: 10px 10px 20px -3px rgba(0,0,0,0.4);
	font-family: Helvetica;
    
    


   
}
.timeline-content h3{
    font-size: 20px;
	color: rgb(59, 171, 245);
	margin:0 0 10px;
	text-transform: capitalize;
	font-weight: 500;
}
.timeline-content p{
    color: #707070;
	font-size: 16px;
	font-weight: 300;
	line-height: 22px;
}

/* responsive */
@media(max-width: 947px){
	.timeline-items::before{
		left: 7px;
	}
	.timeline-item:nth-child(odd){
		padding-right: 0;
		text-align: left;
	}
	.timeline-item:nth-child(odd),
	.timeline-item:nth-child(even){
		padding-left: 37px;
	}
	.timeline-dot{
		left:0;
	}
}
.all 
{
    display: grid;
    grid-template-rows: 100px 1fr;
}
.Education
{
   padding: 2rem;
    color:#3498db;
    font-size: 50px;
    text-align: center;
    font-weight: 600;
    background-color: #e6f5ff;
	text-decoration: underline;
	text-underline-offset: 10px;
	text-decoration-thickness: 0.05rem;
}
.eading
{
    background-color: #e6f5ff;
	font-family: 'Poppins', sans-serif;
}
.timeline-section
{
    display: grid;
    grid-template-columns: 100px 1fr 100px;
}
.Awards
{
    display: grid;
    grid-template-columns: 1.2fr 6fr 1.2fr;
    background-color: #e6f5ff;
    padding: 3rem;

}
.name
{
    text-align: justify;
    width: 100%;

    font-size: 20px;
    font-weight: 500;
    color: #525252;
    font-family:Helvetica;
}
.name li
{
	font-family: Helvetica
}
.block-new
{
    height: 100px;
    padding: 2rem;
	text-align: center;
 
    cursor: pointer;
    transition: transform 0.3s ease-out;

	font-family: Helvetica;
	font-weight: 500;
	-webkit-font-smoothing: antialiased;
    

}
.block-new:hover
{
    transform: scale(1.03);
    color: #060607;
   
}
.social-menu
{
    margin-left: 0px;

}

.awards-heading h2
{
	font-family: 'Poppins', sans-serif;
}

/*Breakpoints*/




@media screen and (max-width: 1403px) {
	.Awards
  
	{
	  display: grid;
	  grid-template-columns: 50px 1fr 50px;
	}

  
  }
  
  

  @media screen and (max-width: 1051px) {
	.Awards
  
	{
	  display: grid;
	  grid-template-columns: 50px 1fr 50px;
	  padding-left: 0px;
	  padding-right: 0px;
	}

  
  }
  

@media screen and (max-width: 947px) {
    .timeline-section{
        display:grid ;
		grid-template-columns: 0px 5fr 0px;
		padding-bottom: 2rem;
    }
    .Education
	{
		font-size: 26px;
		padding: 25px;
		text-align: left;
	}
	.timeline-section
	{
		padding-top: 1rem;
	}
	.Awards
	{
		display: grid;
		grid-template-columns: 0px 1fr 0px;
		background-color: #e6f5ff;
		padding: 0px;
		padding-bottom: 2rem;
		
	}
	.block-new
	{
		padding: 20px;
		font-size: 15px;
	
		font-family: Helvetica;
		font-weight: 500;
		-webkit-font-smoothing: antialiased;
	}
  }



  @media screen and (max-width:350px) {
    .timeline-section{
        display:grid ;
		grid-template-columns: 0px 5fr 0px;
		padding-bottom: 2rem;
		
    }
    .Education
	{
		font-size: 21px;
		padding: 25px;
		text-align: left;
	}
	.timeline-section
	{
		padding-top: 1rem;
		

	
	}
	.timeline-content p 
	{ 
		font-size: 12px
	};
	
	.timeline-content h3 
	{
		font-size: 15px;
	}
	.timeline-content
	{
		padding: 20px;
	}
	.block-new
	{
		font-size: 13px;
	}
  }


 


  @media screen and (max-width:310px) {
	.timeline-content h3 
	{
		font-size: 15px;
	}
    
	.block-new
	{
		font-size: 12px;
	}
	.timeline-content p 
	{ 
		font-size: 11px
	};
	
  }

